@import 'environment';

h2, h3, h4, h5, h6 {
    color: $dark;
    font-weight: 400;
    text-transform: uppercase;
}

#header {
    background-image: url('../images/duotone.jpg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

#hero {
    margin-top: -84px;
    margin-bottom: 0;
    padding: $spacer;
    padding-top: $spacer * 6 !important;
    
    p {
        font-family: $hero-font-family;
        max-width: 660px;
    }

    h1 {
        font-weight: $headings-font-weight;

        @include media-breakpoint-down(md) {
            font-size: 3rem;
        }

        @include media-breakpoint-down(xs) {
            font-size: 2rem;
        }
    }
}

/* [1] The container */
/* [2] Transition property for smooth transformation of images */
#avatar {
    transform-origin: 0 0;
    transition: transform .5s ease;
}

/* [3] Finally, transforming the image when container gets hovered */
#avatar:hover {
    transform: scale(3);
}

#main {
    background-image: url('../images/duotone-aurora.jpg');
    background-size: contain;
    background-position: bottom center;
    background-repeat: no-repeat;
}

#skills {
    margin-top: 6rem;
    margin-bottom: 6rem;

    .card-icon > svg {
        width: 72px;
        height: 72px;
    }
}

.box-heading {
    width: 100%;
    height: 300px;
    overflow: hidden;
    position: relative;

    @include media-breakpoint-down(md) {
        height: 142px;
    }

    span {
        position: absolute;
        color: $white;
        font-size: 10rem;
        line-height: 1;
        top: -2rem;
        left: -1rem;
        font-family: $headings-font-family;
        font-weight: $headings-font-weight;
    }

    h2 {
        position: absolute;
        bottom: -1rem;
        right: -0.7rem;
        width: 100%;
        text-align: right;
        font-weight: bold;
        font-size: 5rem;
        word-wrap: break-word;
        line-height: 0.8;

        // @media (min-width: 480px) and (max-width: map-get($grid-breakpoints, lg) - 1) {
        //     font-size: 6rem;
        // }
        // @include media-breakpoint-up(lg) {
        //     font-size: 4.3rem;
        // }
    }
}

#nav-showcase {
    .nav-item {
        border-radius: 2rem;
        cursor: pointer;
        color: $warning;
        border: 1px solid $warning;

        &.active {
            color: $dark;
            background-color: $warning;
        }

        &:not(:first-of-type) {
            border-bottom-left-radius: 0;
            border-top-left-radius: 0;
            border-left: 0;
        }

        &:not(:last-of-type) {
            border-bottom-right-radius: 0;
            border-top-right-radius: 0;
        }        
    }
}

#social {
    a {
        text-decoration: none;
        
        &:not(:last-child) {
            margin-right: $spacer * 4;

            @include media-breakpoint-down(xs) {
                margin-right: $spacer * 2;
            }
        }
    }

    svg {
        width: 48px;
        height: 48px;
    }
}

.nav-next {
    opacity: 0.3;
    transition: opacity .15s linear;

    &:hover {
        opacity: 1;
    }
}