// $box-shadow:                  0 .5rem 1rem rgba($black, .15) !default;
// $box-shadow-lg:               0 1rem 3rem rgba($black, .175) !default;

.text-shadow-sm { text-shadow: 0 .1rem .1rem rgba($black, .40) !important; }
.text-shadow { text-shadow: 0 .1rem .2rem rgba($black, .60) !important}
.text-shadow-lg { text-shadow: 0 .2rem .2rem rgba($black, .60) !important; }
.text-shadow-none { text-shadow: none !important; }