
$font-family-base: 'Roboto', 'Arial', 'Open Sans', sans-serif;
$hero-font-family: 'PT Serif', "Times New Roman", Times, serif;
$headings-font-family: 'Montserrat', 'Arial', 'Open Sans', sans-serif;
$headings-font-weight: 700;
$headings-color: rgba(#000, .7);
$body-color: $headings-color;

$border-radius: 1.2rem; // .25rem
$border-radius-lg: 1.6rem; // .3rem
$border-radius-sm: 1.2rem; // .2rem

$tooltip-border-radius: .3rem;

// $input-btn-padding-y: // .375rem
$input-btn-padding-x: 1rem; // .75rem
// $input-btn-padding-y-sm: // .25rem
$input-btn-padding-x-sm: 1rem; // .5rem
// $input-btn-padding-y-lg: // .5rem
$input-btn-padding-x-lg: 1rem; // 1rem

$font-size-base: 1rem;
$line-height-base: 1.6;
// $spacer: 1.3;
$h1-font-size: $font-size-base * 2.5;
$h2-font-size: $font-size-base * 2;
$h3-font-size: $font-size-base * 1.5;
$h4-font-size: $font-size-base * 1.30;
$h5-font-size: $font-size-base * 1.25;
$h6-font-size: $font-size-base;

$success: #32db8a;
$warning: #ffba42;
// $danger: #8C3111;
// $info: #0C1966;
$dark: #221c5a;
$primary: #e64b77;;